import React, { useState, useEffect, forwardRef } from 'react'
import posed, { PoseGroup } from 'react-pose'
import Layout from '../components/layout'

// /!\ YOUR ATTENTION PLEASE /!\
// The code below is horrible
// You have been warned...

const transition = { type: 'spring', stiffness: 300, damping: 50 }

const Line = posed.div({
  pre: {
    opacity: 0,
    transition: { duration: 500 },
  },
  enter: {
    opacity: 1,
    transition: { duration: 500 },
  },
  exit: {
    opacity: 0,
    transition: { duration: 500 },
  },
})

const LineT = posed.div({
  pre: {
    opacity: 0,
    x: ({ preX }) => preX,
    transition,
  },
  enter: {
    opacity: 1,
    x: 0,
    transition,
  },
  exit: {
    opacity: 0,
    x: ({ exitX }) => exitX,
    transition,
  },
})

const Somewhere = posed(
  forwardRef(({ children, style }, ref) => (
    <div ref={ref} style={style}>
      <div
        style={{
          fontSize: 13,
          color: '#b1b1b1',
          position: 'absolute',
          margin: 'auto',
          transform: 'translate(108px, -47px)',
        }}
      >
        {normalize(`tmp: ${children}`)}
      </div>
    </div>
  ))
)({
  pre: {
    opacity: 0,
    x: 0,
    y: 0,
    transition: { duration: 500 },
  },
  enter: {
    opacity: 1,
    x: 0,
    y: 0,
    transition: { duration: 500 },
  },
  exit: {
    opacity: 0,
    x: -10,
    y: -10,
    transition,
  },
})

const steps = [
  [
    <LineT preX={40} exitX={-87} key="topic_branch">
      {normalize('      E---F---G  topic')}
    </LineT>,
    <LineT preX={40} exitX={32} key="fork_point">
      {normalize('     /')}
    </LineT>,
    <Line key="master">{normalize('A---B---C  master')}</Line>,
  ],
  [
    <LineT preX={87} exitX={50} key="just_topic">
      {normalize('        topic')}
    </LineT>,
    <LineT preX={-32} exitX={10} key="arrow">
      {normalize('        ↓')}
    </LineT>,
    <Line key="master">{normalize('A---B---C  master')}</Line>,
    <Somewhere key="somewhere">E---F---G</Somewhere>,
  ],
  [
    <LineT preX={-40} exitX={-40} key="just_topic3">
      {normalize("          E'--F'--G' topic")}
    </LineT>,
    <LineT preX={-10} exitX={-40} key="arrow3">
      {normalize('         /')}
    </LineT>,
    <Line key="master">{normalize('A---B---C  master')}</Line>,
  ],
]

function Rebase() {
  const [step, setStep] = useState(0)
  return (
    <Layout>
      <div
        style={{
          width: 272,
          marginRight: 'auto',
          marginLeft: 'auto',
          userSelect: 'none',
          position: 'relative',
        }}
      >
        <figure
          style={{ cursor: 'pointer' }}
          onClick={() => setStep((step + 1) % steps.length)}
        >
          <div className="mono" style={{ fontSize: 16 }}>
            <PoseGroup preEnterPose="pre">{steps[step]}</PoseGroup>
          </div>
          <figcaption style={{ marginTop: 15 }}>
            Step {step} (tap to animate)
          </figcaption>
        </figure>
      </div>
    </Layout>
  )
}

function normalize(text) {
  return text.replace(/\s/g, '\u00A0')
}

export default Rebase
